import React, { useEffect, useState } from "react"
import { GET_ARTICLES } from "../api"
import styled from "styled-components"
import Dot from "../widget/Dot"
import { useHistory } from "react-router-dom"

const E = {
  ListContent: styled.div`
    padding: 20px;
    width: 800px;
    margin: 0 auto;
    border: 2px dashed #ccc;
    border-top: none;
    border-bottom: none;
  `,
  List: styled.div`
    display: flex;
    align-items: center;
    padding: 10px 0px;
  `,
  Icon: styled.img`
    width: 30px;
    height: 30px;
    border-radius: 30px;
    margin-right: 10px;
  `,
  SubTitle: styled.div`
    font-size: 13px;
    color: #aaa;
  `,
  Title: styled.div`
    cursor: pointer;
    :hover {
      text-decoration: underline;
    }
  `,
}

export default () => {
  const history = useHistory()

  const [list, _list] = useState([])

  useEffect(() => {
    GET_ARTICLES({ size: 888 }).then((rs) => {
      console.log(rs)
      _list(rs)
    })
  }, [])
  return (
    <E.ListContent>
      {list.map(
        ({
          avatar_url, // "http://static.taswell.cn/icon/u=216605226,3652567530&fm=11&gp=0.jpg"
          comment_num, // "0"
          create_time, // "2020-07-27 10:08:32"
          email, // "1058747601@qq.com"
          id, // 777
          name, // "娃哈哈"
          preview_content, // "↵a↵↵a↵新的houseitem，面积变成价格了↵↵ ↵↵ ↵"
          preview_img, // (2) ["http://static.taswell.cn/18/1595815710107image.png?x-oss-process=style/small", "http://static.taswell.cn/57855C971FF740B46EAE8F7FE…/1596075958804image.png?x-oss-process=style/small"]
          tag, // ""
          title, // "dfy"
          uid, // "18"
          update_time, // null
        }) => {
          return (
            <E.List key={id}>
              <E.Icon src={avatar_url} alt="" />
              <div>
                <E.Title
                  onClick={() => {
                    history.push(`/article/${id}`)
                  }}
                >
                  {title || "NO TITLE"}
                </E.Title>
                <E.SubTitle>
                  {name}
                  <Dot />
                  {create_time}
                </E.SubTitle>
              </div>
            </E.List>
          )
        }
      )}
    </E.ListContent>
  )
}
