import React from "react"
import { HashRouter as Router, Switch, Route } from "react-router-dom"
import Home from "./page/Home"
import Article from "./page/Article"

export default () => (
  <Router>
    <Switch>
      <Route path="/" exact>
        <Home />
      </Route>
      <Route path="/article/:id" exact>
        <Article />
      </Route>
    </Switch>
  </Router>
)
